import React from 'react'

export default function NotFound({ lang = "ko" }) {
    const messages = {
        ko: "   ",
        ja: "",
        en: "Page not found"
    }
    return (
    <div>
        <h1>
            404
        </h1>
        <p>
            {messages[lang]}
        </p>
    </div>
)

}
