import { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import axios from 'axios';
import Search_ja from '../component/Search_ja';
import Language from '../component/Language'
import '../component/css/reset.css';
import './css/searchresultpage.css';
import './css/searchresultpage-mobile.css';

const BASE_URL = 'https://birthdaycountdownserver.onrender.com';

function useQuery() {
    //url search query   
    return new URLSearchParams(useLocation().search);
}

function SearchResultPage_ja() {
    //query q       ,   
    const query = useQuery().get('q') || '';
    console.log(query);
    const [results, setResults] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        //query   
        if (!query) {
            setLoading(false);
            return;
        }
        //      
        const fetchData = async () => {
            try {
                setLoading(true);
                // '/' API 
                //API query    
                const response = await axios.get(`${BASE_URL}/search?q=${encodeURIComponent(query)}`);
                //   setResult  
                setResults(response.data);
            } catch (error) {
                setError("");
            } finally {
                setLoading(false);
            }
        };
        fetchData();
        //query  
    }, [query]);


    return (
        <div className='resultpage-wrap'>
            <Language />
            <Search_ja className="Search"></Search_ja>
            <div className='resultpage'>
                <h2>
                    {query}  {results.length}
                </h2>

                {/*  */}
                {loading && <p></p>}

                {/*   */}
                {error && <p style={{ color: 'red' }}>{error}</p>}

                {/*      */}
                {!loading && !error && (
                    results.length > 0 ? (
                        <ul>
                            {results.map((item, idx) => (
                                <li key={idx}>
                                    <Link to={`/ja/${slugify(item.en)}`} style={{color: item.color}}>
                                        <div style={{backgroundColor: item.color}}>
                                            {item.ja} 
                                        </div>
                                        {item.ja}
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p>
                            
                        </p>
                    )
                )}
            </div >
        </div >
    );
}
function slugify(str) {
    return str
        .toLowerCase()
        .replace(/[^a-z0-9\s-]/g, '') //  
        .replace(/\s+/g, '-')         //   
        .trim();
}

export default SearchResultPage_ja;