function slugify(str) {
  if (typeof str !== 'string') return '';
  return str
    .toLowerCase()
    .normalize('NFD') //   ()
    .replace(/[^\p{L}\p{N}\s-]/gu, '')  //  : , , ,   ( )
    .replace(/\s+/g, '-')               //   
    .trim();
}

