import './css/reset.css';
import './css/title.css';
import './css/title-mobile.css';
import React from 'react'
import { Link } from 'react-router-dom';

function Title_ja({className}) {
    return (
        <header className={className}>
            <h1>
                <Link to={`/ja`}> </Link>
            </h1>
        </header>
    )
}

export default Title_ja
