import { useState, useMemo } from 'react';
import { Link, useLocation } from 'react-router-dom';
import './css/reset.css';
import './css/character.css';
import './css/character-mobile.css';

function slugify(str) {
    if (typeof str !== 'string') return '';
    return str
        .toLowerCase()
        .normalize('NFD')
        .replace(/[^\p{L}\p{N}\s-]/gu, '')
        .replace(/\s+/g, '-')
        .trim();
}

function getName(item, lang = 'ko') {
    if (lang === 'ja') return item.ja;
    if (lang === 'en') return item.en;
    return item.ko;
}

export default function Character({ items }) {
    const { pathname } = useLocation();
    const lang = 
        pathname.startsWith('/ja') ? 'ja' :
        pathname.startsWith('/en') ? 'en' :
        'ko';
        const basePath =
        lang === 'ja' ? '/ja' :
        lang === 'en' ? '/en' :
        '';

    const safeItems = Array.isArray(items) ? items : [];

    //: groupId  
    const units = useMemo(
        () => safeItems.filter((item) => !item.groupId),
        [safeItems]
    );

    //  :  
    const [activeUnitId, setActiveUnitId] = useState(units[0]?.id ?? null);

    if (units.length === 0) {
        return
        <div style={{ textAlign: 'center' }}>
              .
        </div>;
    }

    return (
        <div className='unit-char-tabs'>
            {units.map((unit) => {
                const isActive = activeUnitId === unit.id;

                //    
                const unitMembers = safeItems.filter(
                    (item) =>
                        item.groupId &&
                        getName(item.groupId, lang) === getName(unit, lang)
                );
                

                return (
                    <div
                        key={unit.id}
                        className='unit-selection'
                    >
                        {/*   */}
                        <button
                            className={
                                'unit-tab-btn' +
                                (isActive ? ' active' : '')
                            }
                            onClick={() => setActiveUnitId(unit.id)}
                        >
                            {getName(unit, lang)}
                        </button>

                        {/*    */}
                        {isActive && (
                            <div className='character-tab-row'>
                                {unitMembers.map((member) => (
                                    <Link
                                        key={member.id}
                                        to={`${basePath}/${slugify(getName(member, lang))}`}
                                        className='character-pill-link'
                                    >
                                        <button
                                            className='character-pill'
                                            style={{
                                                borderColor: member.color || '#ccc',
                                                backgroundColor: member.color || '#fff',
                                            }}
                                        >
                                            <span className='character-pill-label'>
                                                {getName(member, lang)}
                                            </span>
                                        </button>
                                    </Link>
                                ))}

                                {unitMembers.length === 0 && (
                                    <div className='no-members'>
                                            
                                    </div>
                                )}
                            </div>
                        )}
                    </div>
                );
            })}
        </div>
    );
};
