import './App.css';
import './component/css/reset.css';
import { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, useLocation, Outlet } from 'react-router-dom';
//  
import Home from './pages/Home';
import SearchResultPage from './pages/SearchResultPage';
import CharacterPage from './pages/CharacterPage';
// 
import Home_ja from './pages/Home_ja';
import SearchResultPage_ja from './pages/SearchResultPage_ja';
import CharacterPage_ja from './pages/CharacterPage_ja';
// 
import Home_en from './pages/Home_en';
import SearchResultPage_en from './pages/SearchResultPage_en';
import CharacterPage_en from './pages/CharacterPage_en';
//not found 
import NotFound from './pages/NotFound';

function LangSetter() {
  const { pathname } = useLocation();

  useEffect(() => {
    const path = pathname.toLowerCase();
    document.documentElement.lang = 
    path.startsWith('/ja') ? 'ja' : 
    path.startsWith('/en') ? 'en' : 'ko';
    document.documentElement.dir = 'ltr'
  }, [pathname]);
  return null;
}

function LocaleLayout() {
  return <Outlet />
}

function App() {
  const [items, setItems] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const url = `${process.env.PUBLIC_URL || ''}/item.json`  //  
    console.log('item.json  URL: ', url)
    fetch(url)
      .then((res) => {
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        return res.json();
      })
      .then((data) => {
        console.log('item.json  : ', data.length, '');
        setItems(data);
        setLoading(false);
      })
      .catch((e) => {
        console.error('item.json   : ', e);
        setItems([]);
        setLoading(false);
      });
  }, []);

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL || '/'}>
      <LangSetter />
      <div className="App">
        <Routes>
          {/*   */}
          <Route path="/" element={<Home items={items} />} />
          <Route path='/search' element={<SearchResultPage />} />
          <Route
            path="/:slug"
            element={<CharacterPage items={items} />}
          />
          <Route path='*' element={<NotFound lang='ko' />} />
          {/*   */}
          <Route path='/ja' element={<LocaleLayout />}>
            <Route index element={<Home_ja items={items} />} />
            <Route path='search' element={<SearchResultPage_ja />} />
            <Route
              path=":slug"
              element={<CharacterPage_ja items={items} />}
            />
            <Route path='*' element={<NotFound lang='ja' />} />
          </Route>
          {/*   */}
          <Route path='/en' element={<LocaleLayout />}>
            <Route index element={<Home_en items={items} />} />
            <Route path='search' element={<SearchResultPage_en />} />
            <Route
              path=":slug"
              element={<CharacterPage_en items={items} />}
            />
            <Route path='*' element={<NotFound lang='en' />} />
          </Route>
        </Routes>
      </div>
    </BrowserRouter >
  );
}

export default App;