import { useParams, Link } from 'react-router-dom';
import './css/characterpage.css';
import Search_ja from '../component/Search_ja';
import Countdown_ja from '../component/Countdown_ja';
import Language from '../component/Language'
import '../component/css/search-mobile.css';
import './css/characterpage-mobile.css';
import Title_ja from '../component/Title_ja';

function CharacterPage_ja({ items }) {
    const { slug } = useParams();
    console.log ('CharacterPage slug:', slug);
    console.log ('CharacterPage items:', items, 'length:', items && items.length);
    
    if (!Array.isArray(items) || items.length === 0) {
        return <div style={{ textAlign: 'center' }}>
            
        </div>
    }
    const normalizedSlug = slugify(slug);
    console.log('normalizaedSlug:', normalizedSlug);

    const unitMembers = items.filter((item) => {
        const group = item.groupId || {};
        const slugs = [
            slugify(group.ko),
            slugify(group.en),
            slugify(group.ja)
        ];
        return slugs.includes(normalizedSlug);
    });
    console.log('unitMenbers.length =', unitMembers.length);

    const character = items.find((item) => {
        const slugs = [
            slugify(item.ko),
            slugify(item.en),
            slugify(item.ja)
        ];
        return slugs.includes(normalizedSlug);
    });
    console.log('character = ', character);

    const unitName = unitMembers.length > 0 ? unitMembers[0].groupId.ja : null;
    const unitColor = 
        unitMembers.length > 0
            ? (unitMembers[0].color || '#fff')
            : '#fff';
    


    if (unitMembers.length > 0) {
        return (
                <div className='contents'>
                    <Language />
                    <Title_ja className="Title" />
                    {/* <Search_ja className='Search-box'></Search_ja> */}
                    <h1 style={{ color: unitColor || '#ffffff' }} lang='ja'>{unitName}</h1>
                    <ul>
                        {unitMembers.map((member) => (
                            <li key={member.ja}>
                                <Link to={`/ja/${slugify(member.en)}`}>
                                    <div className='imgbox' style={{ backgroundColor: member.color || '#ffffff' }}>
                                        {member.ja} 
                                    </div>
                                    <div className='txt' style={{ color: member.color || '#ffffff' }}>{member.ja}</div>
                                </Link>
                            </li>
                        ))}
                    </ul>
                </div>
        );
    }

    if (character) {
        return (
            <div>
                <Language />
                <Title_ja className="Title" />
                {/* <Search_ja className='Search-box'></Search_ja> */}
                <Countdown_ja member={character}></Countdown_ja>
            </div>
        );
    }

    return <h1></h1>;
}

function slugify(str) {
    if (typeof str !== 'string') return '';
    return str
        .toLowerCase()
        .normalize('NFD')
        .replace(/[^\p{L}\p{N}\s-]/gu, '')
        .replace(/\s+/g, '-')
        .trim();
}

export default CharacterPage_ja;