import React, { useEffect, useState } from 'react';
import './css/reset.css';
import './css/countdown.css';
import './css/countdown-mobile.css';

function Countdown({ member }) {
    const [timeLeft, setTimeLeft] = useState(null);
    const [isBirthdayToday, setIsBirthdayToday] = useState(false); //   

    //member   
    const { birthday, color, firstNameKo, ko} = member || {};

    const calculateTimeLeft = (targetDate) => {
        const difference = +targetDate - +new Date();
        return difference > 0
            ? {
                days: Math.floor(difference / (1000 * 60 * 60 * 24)),
                hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
                minutes: Math.floor((difference / (1000 * 60)) % 60),
                seconds: Math.floor((difference / 1000) % 60),
            }
            : { days: 0, hours: 0, minutes: 0, seconds: 0 };
    };

    const getNextBirthday = (birthday) => {
        if (!birthday) return null;
        const today = new Date();
        const currentYear = today.getFullYear();
        const [month, day] = birthday.split('-').map(Number);

        const birthdayDate = new Date(currentYear, month - 1, day);
        if (today > birthdayDate) birthdayDate.setFullYear(currentYear + 1);
        return birthdayDate;
    };

    //    
    const checkIsBirthdayToday = (birthday) => {
        if (!birthday) return false;
        const today = new Date();
        const [month, day] = birthday.split('-').map(Number);
        return (
            today.getMonth() === month - 1 &&
            today.getDate() === day
        );
    };

    useEffect(() => {
        if (!birthday) return;
        
        //   
        const birthdayToday = checkIsBirthdayToday(birthday);
        setIsBirthdayToday(birthdayToday);

        if(birthdayToday) {
            setTimeLeft(null);
            return;
        }

        const nextBirthday = getNextBirthday(birthday);
        setTimeLeft(calculateTimeLeft(nextBirthday));

                const timer = setInterval(() => {
                    setTimeLeft(calculateTimeLeft(nextBirthday));
                }, 1000);

                return () => clearInterval(timer);
            }, [birthday]);

            if (!birthday) {
                return (
                    <div style={{margin: "auto", textAlign: "cneter", color: "red"}}>
                          
                    </div>
                )
            }

    if (!timeLeft && !isBirthdayToday) return <div style={{margin: "auto", textAlign: "center"}}> ...</div>;

    //    
    if (isBirthdayToday) {
        return (
            <div style={{
                padding: '2rem',
                textAlign: 'center',
                color: color || '#fff',
                backgroundColor: '#fff',
                borderRadius: '1rem'
            }} className='isbirthday'>
                <h2>{firstNameKo || ko} <br />  !</h2>
                <p> {ko} !</p>
            </div>
        );
    }

    //   
    return (
        <div className='countdown-wrap'>
            <h2 style={{ color: color }}>{ko}  </h2>
            <div style={{ color: color }}>
                <div className='time'>
                    <span>
                        {timeLeft.days}
                    </span>
                    <span>
                        {timeLeft.hours}
                    </span>
                    <span>
                        {timeLeft.minutes}
                    </span>
                    <span>
                        {timeLeft.seconds}
                    </span>
                </div>
                <p>!</p>
            </div>
        </div>
    );
}

export default Countdown;
