import './css/reset.css';
import './css/search.css';
import './css/search-mobile.css';
import React, { useState } from 'react';
import {useNavigate} from 'react-router-dom';

function Search_ja(props) {
    const [query, setQuery] = useState('');
    const navigate = useNavigate();

    const handleSearch = () => {
        //        
        if (query.trim()) {
            //  
            navigate(`/ja/search?q=${encodeURIComponent(query)}`);
        }
    };

    const EnterPush = txt => {
        //Enter  handleSearch 
        if (txt.key === 'Enter') handleSearch();
    };

    return (
        <div className={`search-wrap ${props.className}`}>
            <input
            //  
                type='search'
                //   query  
                value={query}
                //    setQuery    
                onChange={
                    txt => setQuery(txt.target.value)
                }
                //Enter  
                onKeyDown={EnterPush}
                // 
                placeholder='/'
                className='search-table'
                />
                {/*    */}
            <button onClick={handleSearch} className='search-btn'>
                
            </button>
        </div>
    )
}

export default Search_ja
